const SEND_MESSAGE = 'ADD-MESSAGE';

let initialState = {
    dialogs: [
        {
            id: 1,
            name: 'Dimych',
            img: 'https://i.pinimg.com/originals/0c/a9/e2/0ca9e28dcb12dc698cfd2beda6d6fa64.jpg'
        },
        {
            id: 2,
            name: 'Andrey',
            img: 'https://www.meme-arsenal.com/memes/cd2652ae9d5c44e3c695d72fd37f647e.jpg'
        },
        {
            id: 3,
            name: 'Sveta',
            img: 'data:image/jpeg;base64,'
        },
        {
            id: 4,
            name: 'Sasha',
            img: 'data:image/jpeg;base64,'
        },
        {
            id: 5,
            name: 'Victor',
            img: 'data:image/jpeg;base64,'
        },
        {
            id: 6,
            name: 'Valera',
            img: 'data:image/jpeg;base64,'
        },

    ],
    messages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'How is you?'},
        {id: 3, message: 'Yo'}
    ],
};

const messageReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE : {
            let newMessage = {
                id: 4,
                message: action.newMessageText
            };
            return {
                ...state,
                messages: [...state.messages, newMessage], //push newMessage
                newMessageText: ''
            };
        }
        default :
            return state;
    }
};

export const sendMessageActionCreator = (newMessageText) => ({type: SEND_MESSAGE, newMessageText});

export default messageReducer;